/* 1. Use a more-intuitive box-sizing model */
*,
*::before,
*::after {
	box-sizing: border-box;
}

/* 2. Remove default margin */
* {
	margin: 0;
	font-family: 'roboto';
}

body {
	/* 3. Add accessible line-height */
	line-height: 1.5;
	/* 4. Improve text rendering */
	-webkit-font-smoothing: antialiased;
}

/* 5. Improve media defaults */
img,
picture,
video,
canvas,
svg {
	display: block;
	max-width: 100%;
}

/* 6. Inherit fonts for form controls */
input,
button,
textarea,
select {
	font: inherit;
}

/* 7. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	overflow-wrap: break-word;
}

/* 8. Improve line wrapping */
p {
	text-wrap: pretty;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	text-wrap: balance;
}

/*
  9. Create a root stacking context
*/
#root,
#__next {
	isolation: isolate;
}

/*****/
/* PROJECT CSS */
/*****/

.container {
	display: grid;
	grid-template-columns: 1fr 5fr;
	height: 90vh;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,cAAc;AACd,6CAA6C;AAC7C;;;CAGC,sBAAsB;AACvB;;AAEA,6BAA6B;AAC7B;CACC,SAAS;CACT,qBAAqB;AACtB;;AAEA;CACC,kCAAkC;CAClC,gBAAgB;CAChB,8BAA8B;CAC9B,mCAAmC;AACpC;;AAEA,8BAA8B;AAC9B;;;;;CAKC,cAAc;CACd,eAAe;AAChB;;AAEA,uCAAuC;AACvC;;;;CAIC,aAAa;AACd;;AAEA,4BAA4B;AAC5B;;;;;;;CAOC,yBAAyB;AAC1B;;AAEA,6BAA6B;AAC7B;CACC,iBAAiB;AAClB;;AAEA;;;;;;CAMC,kBAAkB;AACnB;;AAEA;;CAEC;AACD;;CAEC,kBAAkB;AACnB;;AAEA,MAAM;AACN,gBAAgB;AAChB,MAAM;;AAEN;CACC,aAAa;CACb,8BAA8B;CAC9B,YAAY;AACb","sourcesContent":["/* CSS RESET */\n/* 1. Use a more-intuitive box-sizing model */\n*,\n*::before,\n*::after {\n\tbox-sizing: border-box;\n}\n\n/* 2. Remove default margin */\n* {\n\tmargin: 0;\n\tfont-family: 'roboto';\n}\n\nbody {\n\t/* 3. Add accessible line-height */\n\tline-height: 1.5;\n\t/* 4. Improve text rendering */\n\t-webkit-font-smoothing: antialiased;\n}\n\n/* 5. Improve media defaults */\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n\tdisplay: block;\n\tmax-width: 100%;\n}\n\n/* 6. Inherit fonts for form controls */\ninput,\nbutton,\ntextarea,\nselect {\n\tfont: inherit;\n}\n\n/* 7. Avoid text overflows */\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n\toverflow-wrap: break-word;\n}\n\n/* 8. Improve line wrapping */\np {\n\ttext-wrap: pretty;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n\ttext-wrap: balance;\n}\n\n/*\n  9. Create a root stacking context\n*/\n#root,\n#__next {\n\tisolation: isolate;\n}\n\n/*****/\n/* PROJECT CSS */\n/*****/\n\n.container {\n\tdisplay: grid;\n\tgrid-template-columns: 1fr 5fr;\n\theight: 90vh;\n}"],"sourceRoot":""}]);
